import { NgModule } from '@angular/core';

import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatButtonModule } from '@angular/material/button';
import { MatCardModule } from '@angular/material/card';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatInputModule } from '@angular/material/input';
import { MatProgressBarModule } from '@angular/material/progress-bar';
import { Routes, RouterModule } from '@angular/router';
import { ForgotPasswordAdminComponent } from './forgot-password.component';
import { HomeAdminHeaderModule } from '../../home-admin-header/home-admin-header.module';

const routes: Routes = [{ path: '', component: ForgotPasswordAdminComponent }];
@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    MatProgressBarModule,
    MatCardModule,
    MatFormFieldModule,
    FormsModule,
    MatButtonModule,
    ReactiveFormsModule,
    MatInputModule,
    HomeAdminHeaderModule,
  ],
  declarations: [ForgotPasswordAdminComponent],
  providers: [],
})
export class ForgotPasswordAdminModule {}
