import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';
import { ForgotPasswordService } from './forgot-password.service';
@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.scss'],
})
export class ForgotPasswordComponent implements OnInit {
  email: string;
  showProgressBar = false;
  isDone = false;
  constructor(
    private _router: Router,
    private __service: ForgotPasswordService,
    private __snackbar: MatSnackBar
  ) {}

  ngOnInit() {
    localStorage.removeItem(environment.key_storage.TOKEN.CLIENT)
  }
  ngOnDestroy() {
    this._router.navigate(['index']);
  }
  sendRequire() {
    this.showProgressBar = true;
    this.__service.sendRequire(this.email).subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data === 'true') {
          this.isDone = true
        } else {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
        }
      },
      (error) => {
        this.showProgressBar = false;
        this.__snackbar.open('Có lỗi xảy ra', null, {
          duration: 2000,
        });
      }
    );
  }
}
